import Vue from 'vue'
import Vuex from 'vuex'

import * as API from '@/api/trainer-api'
import * as highlight from '@/text-interactions/highlight-text'
import * as texoo from '@/api/texoo-datamodel'
import * as parser from '@/api/parse-response'

import placeholder_response from '@/store/placeholder.json'

Vue.use(Vuex)

const store = new Vuex.Store(
  {
    state: {
      pageNumber: 0,
      num:1,
      samples: [],
      documents: [],
      // task: 'addMentions'
      task: 'addRelations'
    },
    mutations: {
      setPageNumber (state, pageNumber) {
        state.pageNumber = pageNumber
        localStorage.setItem('pageNumber', state.pageNumber)
      },

      reOpenClosedSamples (state) {
        for (let sample of state.samples) sample.finished = false
      },

      setSamples (state, samples) {
        state.samples = samples
      },

      setDocuments (state, documents) {
        state.documents = documents
      },

      highlightAnnotations (state, content) {
        state.documents = highlight.highlightAnnotationsInSamples(state.documents)
        state.samples = highlight.highlightAnnotationsInSamples(state.samples)
      }
    },
    actions: {
      getSamples (context, pageNumber) {
        // TODO: dev code to add relation start, muss entfernt werden!
        context.state.pageNumber = pageNumber
        let amount = context.state.num
        let samples = parser.instantiateSamples(placeholder_response)
        console.log(samples)
        samples = samples.slice(pageNumber*amount, (pageNumber+1)*amount)
        context.commit('setSamples', samples)
        context.commit('highlightAnnotations')
        // TODO: dev code ende
      },

      getSample (context, uid) {
        API.getSamples(uid)
          .then(sample => {
            context.commit('setSamples', sample)
            context.commit('highlightAnnotations')
          })
      },

      getDocuments (context, pageNumber) {
        API.getDocuments(pageNumber)
        .then(documents => {
          context.commit('setDocuments', documents)
          context.commit('highlightAnnotations')
        })
      },

      getDocument (context, uid) {
        API.getDocuments(uid)
        .then(document => {
          context.commit('setDocuments', document)
          context.commit('highlightAnnotations')
        })
      },

      saveState (context) {
        API.saveState(context.state.samples)
        context.commit('setSamples', []) // set samples array empty
      },

      updateState (context) {
        API.updateState(context.state.samples)
      }
    }
  }
)

export default store



