import axios from 'axios'
import Response from '@/api/response-datamodel'
import * as parser from '@/api/parse-response'

axios.defaults.baseURL = 'http://localhost:8000'
axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.headers['Accept'] = 'application/json'

export function get (resource) {
  return axios.get(resource)
    .then(response => (new Response(response.config.url, response.config.method, response.status, response.data)))
    .catch(error => error)
}

export function getSamples (pageNumber) {
  return get(`/samples/${pageNumber ? `?pageNumber=${pageNumber}` : ``}`)
    // eslint-disable-next-line
    .then(response => parser.instantiateSamples(response))
}

export function getSample (uid) {
  return get('samples/' + uid)
    // eslint-disable-next-line
    .then(response => parser.instantiateSamples(response))
}

export function getDocuments (pageNumber) {
  return get(`/documents/${pageNumber ? `?pageNumber=${pageNumber}` : ``}`)
    // eslint-disable-next-line
    .then(response => parser.instantiateSamples(response))
}

export function getDocument (uid) {
  return get('documents/' + uid)
    // eslint-disable-next-line
    .then(response => parser.instantiateSamples(response))
}

export function saveState (samples) {
  let preparedSamples = parser.prepareSamplesForPOST(samples)
  axios.post('/save', preparedSamples)
}

export function updateState (samples) {
  let preparedSamples = parser.prepareSamplesForPOST(samples)
  axios.post('/update', preparedSamples)
}



